.footer-container {
  background-color: #1c6ca1;
  padding-top: 50px;
  padding-bottom: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.social-wrapper {
  display: flex;
  justify-content: space-between;
  width: 1000px;
}

.social-icon {
  font-size: 1.5rem;
  color: #fff;
  display: flex;
  width: 50px;
  justify-content: space-between;
}

.fa-facebook-square,
.fa-twitter-square {
  color: #fff;
}
